
.slider {
  margin: 0 auto;

  width: 100%;
  max-width: $max-width;
  padding: 85px 84px 50px 82px;
}

.slider h2 {
  margin-bottom: 23px;

  font-family: "Cormorant Garamond", "Times New Roman", serif;
  font-style: normal;
  font-weight: 700;
  font-size: 34px;
  line-height: 44px;
  text-align: center;
  color: #1d1613;
}

.slider__link-catalog {
  position: relative;

  margin-left: 50%;
  transform: translateX(-50%);

  display: inline-block;
  vertical-align: middle;

  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: #1d1613;

  &:hover,
  &:focus {
    color: lighten(#1d1613, 30%);
  }

  &::before {
    content: "";

    position: absolute;
    bottom: 0;

    width: 100%;
    height: 1px;

    background-color: #a87b62;
  }
}

.slider__wrapper {
  margin-top: 60px;
}

.slider__item {
  margin-left: 15px;
  margin-right: 15px;
}

.slider__item img {
  margin: 0 auto;
}

.slider__item h3 {
  margin-top: 22px;
  margin-bottom: 0;

  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  text-align: center;
  color: #1d1613;
}

.slider__item p {
  margin-top: 7px;

  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  color: #1d1613;
}

.slick-arrow {
  position: absolute;
  top: -115px;
  left: -10px;

  width: 50px;
  height: 25px;

  font-size: 0;

  background-image: url("../img/arrow-left.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;

  border: 0;
  cursor: pointer;
}

.slick-next {
  left: auto;
  right: 0;

  background-image: url("../img/arrow-right.svg");
}

.slick-dots {
  @include reset-list;

  margin-top: 35px;

  display: flex;
  justify-content: center;
}

.slick-dots button {
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: #9b9997;

  border: 0;
  background-color: transparent;
  cursor: pointer;
}

.slick-active button {
  color: #1d1613;
}

@media (max-width: $tablet-width) {
  .slider {
    padding: 37px 30px 15px 30px;
  }

  .slider h2 {
    margin-bottom: 17px;
  }

  .slider__wrapper {
    margin-top: 53px;
  }

  .slider__item img {
    width: 324px;
    height: 340px;
  }

  .slick-arrow {
    top: -101px;
    left: 7px;

    background-size: 40px auto;
  }

  .slick-next {
    left: auto;
    right: 7px;
  }

  .slick-dots {
    margin-top: 28px;
  }
}

@media (max-width: $mobile-width) {
  .slider {
    padding: 25px 0 15px 0;
  }

  .slider h2 {
    margin-bottom: 7px;
  }

  .slider__link-catalog {
    width: 50px;

    text-align: center;

    &::before {
      bottom: 5px;
      left: -10px;
      right: -10px;

      width: auto;
    }
  }

  .slider__wrapper {
    margin-top: 35px;
  }

  .slider__item h3 {
    margin-top: 17px;
  }

  .slider__item img {
    width: 130px;
    height: 136px;
  }

  .slick-arrow {
    @include visually-hidden;
  }

  .slick-dots {
    margin-top: 15px;
  }
}
